<template>
	<v-card class="ma-md-6 ma-sm-3">
		<v-card-title class="py-2">
			<v-icon class="mr-3">mdi-face</v-icon>
			Daftar Peserta
		</v-card-title>
		<v-divider></v-divider>
		<v-card-text>
			<v-form ref="form" v-model="valid">
				<v-row dense>
					<v-col cols="12" md="4" sm="6" class="py-0">
						<v-autocomplete
							label="Provinsi"
							outlined
							dense
							v-model="dataFilter.provinsi"
							:items="dataProv"
							item-text="nama"
							item-value="id"
							required
							:rules="requiredRule"
						></v-autocomplete>
					</v-col>
					<v-col cols="12" md="4" sm="6" class="py-0">
						<v-autocomplete
							label="Kabupaten"
							outlined
							dense
							v-model="dataFilter.kabupaten"
							:items="dataKab"
							item-text="nama"
							item-value="id"
							:disabled="dataFilter.provinsi===''?true:false"
							required
							:rules="requiredRule"
						></v-autocomplete>
					</v-col>
					<v-col cols="12" md="4" sm="6" class="py-0">
						<v-autocomplete
							label="Jenjang"
							outlined
							dense
							:disabled="dataFilter.kabupaten===''?true:false"
							v-model="dataFilter.jenjang"
							:items="dataJenjang"
							item-text="nama"
							item-value="id"
							required
							:rules="requiredRule"
						></v-autocomplete>
					</v-col>
					<v-col cols="12" md="4" sm="6" class="py-0">
						<v-autocomplete
							label="Sekolah"
							outlined
							dense
							:disabled="dataFilter.jenjang===''?true:false"
							v-model="dataFilter.sekolah"
							:items="filterSekolah"
							item-text="sekolah"
							item-value="id"
							required
							:rules="requiredRule"
						></v-autocomplete>
					</v-col>
					<v-col cols="12" md="4" sm="6" class="py-0">
						<v-autocomplete
							label="Kelas"
							outlined
							dense
							v-model="dataFilter.kelas"
							:items="dataKelas"
							item-text="nama"
							item-value="id"
							required
							:rules="requiredRule"
						></v-autocomplete>
					</v-col>
					<v-col cols="12" md="4" sm="6" class="py-0">
						<v-autocomplete
							label="Kantor Unit"
							outlined
							dense
							v-model="dataFilter.unit"
							:items="dataUnit"
							item-text="nama"
							item-value="id"
							required
							:rules="requiredRule"
						></v-autocomplete>
					</v-col>
				</v-row>
				<v-btn
					type="submit"
					:disabled="!valid"
					@click.prevent="validate"
					block color="primary"
					depressed>
					filter
				</v-btn>
			</v-form>

			<DataPeserta/>
		</v-card-text>
	</v-card>
</template>

<script>
	import { mapState } from 'vuex'
	import DataPeserta from '@/components/Peserta/DataPeserta'

	export default{
		data: () => ({
			valid: true,
			requiredRule: [v => !!v || 'Data harus diisi'],			
			dataFilter: {
				provinsi: '',
				kabupaten: '',
				jenjang: '',
				sekolah: '',
				kelas: '',
				unit: ''
			}
		}),

		created() {
			this.$store.dispatch('dataTempat/getDataProv')			
			this.$store.dispatch('dataJenjang/getDataJenjang')
			this.$store.dispatch('dataKelas/getDataKelas')
			this.$store.dispatch('dataUnit/getDataUnit')
		},

		computed: {
			...mapState('dataTempat', ['dataProv', 'dataKab']),
			...mapState('dataJenjang', ['dataJenjang']),
			...mapState('dataKelas', ['dataKelas']),
			...mapState('dataUnit', ['dataUnit']),
			...mapState('dataSekolah', ['dataSekolah']),
			filterSekolah() {
				return this.dataSekolah.filter(x => x.jenjang.id === this.dataFilter.jenjang)
			}
		},

		components: {
			DataPeserta
		},

		watch: {
			'dataFilter.provinsi': function (val) {
				this.$store.dispatch('dataTempat/getDataKab', val)
			},
			'dataFilter.jenjang': function (val) {
				this.$store.dispatch('dataSekolah/getDataSekolahFilter', {
					provinsi_id: this.dataFilter.provinsi,
					kabupaten_id: this.dataFilter.kabupaten
				})
			}
		},

		methods: {
			validate () {
				if (this.$refs.form.validate()) {
					console.log(this.dataFilter)
				}
			}
		}
	}
</script>